body{
  background-color: orange;
}

h1 {
  text-align: center;
}

.input-style{
  display: flex;
  justify-content: center;
  align-items:center;
  margin: 5px;
}

#button{
  font-weight: bold;
}

.div{
  display: flex;
  justify-content: center;
  align-items:center;
}

.div-style{
  background-color: #1FCCCE;
  margin: 10px 5px;
  border: 5px solid black;
  border-radius: 10px;
}

.hapus{
  width: 60px;
  height: 30px;
  background-color:#F05B5B;
  margin:0 10px;
  border-radius: 5px;
  font-weight: bold;
}

.edit{
  width: 60px;
  height: 30px;
  background-color: yellow;
  margin-bottom: 10px;
  border-radius: 5px;
  font-weight: bold;
}

.paragraph{
  margin: 10px 10px;
}
